<template>
  <h1>{{ t("cart.title") }}</h1>

  <p class="empty" v-if="$store.getters.cartIsEmpty">{{ t("cart.empty") }}</p>
  <div v-if="!$store.getters.cartIsEmpty" id="container">
    <div
      v-for="item in this.$store.getters.cartItems"
      v-bind:key="item.id"
      class="cartItem"
    >
      <div class="title">
        <p>{{ item.quantity }}x {{ item.language[this.$i18n.locale].title }}</p>
      </div>
      <div class="price">
        <p>€{{ item.quantity * item.price }}</p>
      </div>
    </div>
    <hr />
    <div class="cartItem">
      <div class="title">
        <p>{{ t("cart.subtotal") }}</p>
      </div>
      <div class="price">
        <p>€{{ $store.getters.subtotal }}</p>
      </div>
    </div>
    <div
      class="cartItem"
      v-bind:key="item"
      v-for="item in this.$store.getters.coupons"
    >
      <div class="title">
        <p>
          {{ t("cart.coupon") }} <i>{{ item.id }}</i>
        </p>
      </div>
      <div class="price">
        <p>- €{{ item.value }}</p>
      </div>
    </div>
    <div class="cartItem" v-if="this.$store.getters.requires_shipping">
      <div class="title shipping">
        <p>{{ t("cart.shipping") }}</p>
        <div>
          {{ t("cart.freeshipping") }} €{{
            this.$store.getters.free_shipping_treshold
          }},-
        </div>
      </div>
      <div class="price">
        <p>€{{ $store.getters.shippingCosts }}</p>
      </div>
    </div>
    <div class="cartItem">
      <div class="title">
        <p class="bold">{{ t("cart.total") }}</p>
      </div>
      <div class="price">
        <p class="bold">€{{ $store.getters.totalCosts }}</p>
      </div>
    </div>
  </div>
  <div class="actions">
    <router-link :to="{ name: 'shop', params: { lang: this.$i18n.locale } }">
      <div>{{ t("cart.continue") }}</div></router-link
    >
    <router-link
      v-if="!$store.getters.cartIsEmpty"
      :to="{ name: 'checkout', params: { lang: this.$i18n.locale } }"
      ><div>{{ t("cart.checkout") }}</div></router-link
    >
  </div>
</template>

<script>
import { useI18n } from "vue-i18n";

import shopItems from "../data/shop.json";
export default {
  name: "Cart",
  data() {
    return {
      salt: this.$CryptoJS.enc.Base64.parse("vJslhxLPgGAQ3pm1Ef9nXA=="),
      shopItems: shopItems.catalog,
      coupons: shopItems.coupons,
    };
  },
  mounted() {
    this.$analytics.viewCart(
      this.$store.getters.cartItems,
      this.$store.getters.totalCosts
    );
    if (!this.$store.getters.validUserPrices) {
      this.$router.push({
        name: "shop",
        params: { lang: this.$i18n.locale },
      });
    }
  },
  setup() {
    return useI18n();
  },
  methods: {
    applyCoupon(coupon) {
      let userHash = this.$CryptoJS.PBKDF2(coupon, this.salt, {
        keySize: 512 / 32,
        iterations: 1000,
      });
      let self = this;
      Object.keys(this.coupons).forEach((hash) => {
        if (userHash.toString(self.$CryptoJS.enc.Base64) == hash) {
          this.$store.commit("addCoupon", {
            coupon,
            value: self.coupons[hash].value,
          });
        }
      });
    },
  },
};
</script>

